import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import Pagination from '../Base/Paginations/Pagnations';
import { getAllDepartments } from '../Apis';

//import usersData from './UsersData'
const adddepartment = 'department/add'

function DepartmentRow(props) {
  const departments = props.departments
  const departmentLink = `/departments/${departments.id}`
  const updatedepartment = `department/update/${departments.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={departments.id.toString()}>
      <th scope="row"><Link to={departmentLink}>{departments.id}</Link></th>
      <td className={'nameRow'} ><Link to={departmentLink}>{departments.name}</Link></td>
      <td>{departments.description}</td>
      <td className={'idRow'}>{departments.primary_phone}</td>
      <td className={'editRow'}><Link to={updatedepartment}><span class="cui-pencil" aria-hidden="true"></span></Link></td>
    </tr>
  )
}

class Departments extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      totalPages: 0,
      totalRecords: 0
    };
  }

  componentDidMount() {
    this.getAllDepartments(1)
  
  }
  getAllDepartments=async(page)=>{
    const responseData= await getAllDepartments(page);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
              isLoaded: true,
              items: responseData.data.result,
              totalPages: responseData.data.pages,
              totalRecords: responseData.data.count
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  onPageChanged = data => {
    //const { allCountries } = this.state;
   
    const { currentPage, totalPages, pageLimit } = data;
    this.getAllDepartments(currentPage)
    console.log(data);
  };
  render() {

    const departmentList = this.state.items; //usersData.filter((user) => user.id < 10)
    const Totrecords = parseInt(this.state.totalRecords); // total users records.
    let pagination;

    if(Totrecords !== 0 ){
      pagination = <Pagination totalRecords={Totrecords} pageLimit={5} pageNeighbours={1} onPageChanged={this.onPageChanged} />
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={1}>
          </Col>
          <Col xl={10}>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Departments 
                <Link to={adddepartment}><button onClick={this.showHospitalInfo} className='btn btn-success  userbtn'>+ Add department</button></Link>
              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">Id</th>
                      <th scope="col">Name</th>
                      <th scope="col">Description</th>
                      <th scope="col">Primary Phone</th>
                      <th scope="col">Edit department</th>
                    </tr>
                  </thead>
                  <tbody>
                    {departmentList.map((departments, index) =>
                      <DepartmentRow key={index} departments={departments}/>
                    )}
                  </tbody>
                </Table>
              </CardBody>              
              {pagination}
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Departments;
